        <Popup v-if="detailModal"
               @close-modal="detailModal = false">
            <div class="popup-title">
                <div class="h2">Кредит «{{detailTarrif.name}}»</div>
            </div>
            <div class="popup-body">
                <div class="detail-tarif-info">
                    <p>Кредит на обеспечение исполнения обязательств по контракту в рамках 44-ФЗ, 223-ФЗ</p>
                    <ul>
                        <li>Процентная ставка <span>1.5%</span></li>
                        <li>Макс. срок кредита <span>32 {{$options.filters.declensionNumbers(32, ['месяц', 'месяца', 'месяцев'])}}</span></li>
                        <li>Мин. сумма кредита <span>{{$options.filters.numberFilter(250000)}} {{$options.filters.declensionNumbers(250000, ['рубль', 'рубля', 'рублей'])}}</span></li>
                        <li>Принятие решения <span>до {{returnDate(120)}}</span></li>
                    </ul>
                </div>
                <button class="btn btn-lg btn-block close-modal">Закрыть</button>
            </div>
        </Popup>

    </div>

