<script>

    import {mapState} from 'vuex'
    import Popup from "@/components/Blocks/Popup";

    export default {
        name: "CreditInfoBlock",
        components: {Popup},
        computed: {
            ...mapState({
                orderInfo: state => state.createOrder,
                tariffs: state => state.tariffs.tariffs
            }),
            filteredCreditItems() {
                return this.credits.filter(credit => {
                    let satisfies = true,
                        sum = parseInt(this.orderInfo.creditSum.value.toString().replace(/\s/g, ''))
                    if ( credit.minSum > sum ) {
                        satisfies = false
                    }
                    if ( credit.maxTerm < this.orderInfo.creditTerm.value ) {
                        satisfies = false
                    }
                    return satisfies
                })
            }
        },
        data: () => ({
            load: false,
            detailModal: false,
            detailTarrif: {}
        }),
        methods: {
            returnDate(hours) {
                let date, titles;
                if ( hours > 24 ) {
                    date = hours / 24;
                    titles = ['день', 'дня', 'дней']
                }
                else {
                    date = hours
                    titles = ['час', 'часа', 'часов']
                }
                return `${date} ${this.$options.filters.declensionNumbers(date, titles)}`
            },
            createOrder(tariffId) {
                this.load = true
                this.$store.dispatch('createOrder', tariffId).then(id => {
                    this.$router.push(`/create-order/${id}`).then(() => {
                        this.load = false
                    })
                }).catch(() => {
                    this.load = false
                })
            },
            openDetailModal(tarrif) {
                this.detailTarrif = tarrif
                this.detailModal = true
            }
        },
        mounted() {
            this.$store.dispatch('getTariffs')
        }
    }
</script>
