<template>
    <div class="home">

<!--        <StopFactors />-->

        <div class="credit-selection"
             :class="{'disabled': !canGetCredit}">

            <div class="credit-selection-header">
                <div class="h2">Подбор кредитного продукта</div>
                <a v-if="canGetCredit"
                   href="#"
                   class="clear-link"
                   @click.prevent="$store.commit('resetStateParameters')">
                    <span class="icon-refresh"></span>Сбросить фильтр
                </a>
            </div>

            <div class="credit-selection-body">
                <CreditSelection :disabled="!canGetCredit"/>
            </div>

        </div>

        <section v-if="canGetCredit"
                 class="s-suits-you">

            <div class="section-title">
                <div class="h2">Вам подходит</div>
            </div>

            <CreditInfoBlock/>

        </section>

        <section class="s-orders">

            <div class="title-row">
                <div class="h2">Ваши заявки</div>
                <div class="title-link">
                    <router-link to="/orders">Все заявки</router-link>
                </div>
            </div>

            <OrdersTable :withBtn="false"
                         :quantity="3"/>

        </section>

        <section class="s-other-offers">

            <div class="section-title">
                <div class="h2">Другие предложения банка</div>
            </div>

            <OtherOffers />

        </section>

        <Popup v-if="regbankModal"
               @close-modal="regbankModal = false">
            <div class="popup-title">
                <div class="h2" v-html="$t('home.reg_modal_title')"/>
            </div>
            <div class="popup-body">
                <button class="btn btn-lg btn-block close-modal">Понятно</button>
            </div>
        </Popup>

    </div>
</template>

<script>

    import CreditSelection from '@/components/Blocks/CreditSelection'
    import CreditInfoBlock from '@/components/Blocks/CreditInfoBlock'
    import OrdersTable from '@/components/Blocks/OrdersTable'
    import OtherOffers from "@/components/Blocks/OtherOffers"
    import Popup from "@/components/Blocks/Popup";
    import {mapState} from "vuex";

    export default {
        name: 'Home',
        components: {
            Popup,
            OtherOffers,
            CreditSelection,
            CreditInfoBlock,
            OrdersTable
        },
        computed: {
            ...mapState({
                company: state => state.users.company
            })
        },
        data: () => ({
            regbankModal: false,
            canGetCredit: true
        }),
        mounted() {
            let int;
            int = setInterval(() => {
                if ( !this.company.regbank ) {
                    this.regbankModal = true
                }
                if ( !this.regbankModal ) {
                    this.regbankModal = true
                }
                else {
                    clearInterval(int)
                }
            }, 100)
        }
    }
</script>
